export default {
  name: 'ShareCircle',
  props: {
    info: {
      type: Object,
      default: () => {}
    },
    isTopic: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {}
  },
  methods: {
    shareActivity () {
      this.$emit('shareActivity')
    }
  }
}



