export default {
  name: 'ItemInfo',
  props: {
    itemInfo: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {}
  },
  mounted () {
    console.log(this.itemInfo, 456)
  },
  methods: {
    clickCollection () {
      this.doApp()
    },
    clickLike () {
      this.doApp()
    },
    clickComment () {
      this.doApp()
    },
    doApp () {
      this.$emit('doApp')
    }
  }
}



