import avatar from '@/components/AVATAR/'
import FadeAnimation from '@/components/common/fade/FadeAnimation'
import {commonAction} from '@/utils/commonAction'
export default {
  name: 'morePeople',
  components: {
    avatar,
    FadeAnimation
  },
  props: {
    peopleList: {
      type: Array,
      default: () => []
    },
    maxShowNumber: [Number]
  },
  data () {
    return {
      isUpHeight: false,
      isDownHeight: true,
      arrowPointsTo: 'down',
      filterPeople: [],
      commonHeight: commonAction.pxToRem(90)
    }
  },
  mounted () {
    console.log(this.peopleList, 456789)
    this.filterPeople = this.peopleList.slice(0, 6)
    if (this.peopleList.length < 6) {
      this.commonHeight = commonAction.pxToRem(70)
    }
  },
  methods: {
    clickAvatar () {
      this.$emit('clickAvatar')
    },
    clickUpDown () {
      this.$emit('clickUpDown')
    },
    clickArrow () {
      if (this.arrowPointsTo === 'down') {
        this.filterPeople = this.peopleList.slice(0, this.maxShowNumber)
        this.commonHeight = commonAction.pxToRem(90 + Math.floor(this.filterPeople.length / 6) * 40)
        this.arrowPointsTo = 'up'
      } else {
        this.filterPeople = this.peopleList.slice(0, 6)
        this.commonHeight = commonAction.pxToRem(90)
        this.arrowPointsTo = 'down'
      }
    }
  }
}



